"use strict"

export function initLoadPage() {
   initPageLoader()
}

function initPageLoader(delay = 600, bgColor = '#292F36', barColor = '#0C73B8') {
   // 1. –§–∞—Ä–±—É—î–º–æ –∫–æ—Ä—ñ–Ω—å –¥–æ–∫—É–º–µ–Ω—Ç–∞ –º–∏—Ç—Ç—î–≤–æ
   document.documentElement.style.backgroundColor = bgColor;

   // 2. –°—Ç–≤–æ—Ä—é—î–º–æ —Å—Ç–∏–ª—ñ –¥–ª—è body —Ç–∞ –ø—Ä–æ–≥—Ä–µ—Å-–±–∞—Ä—É
   const style = document.createElement('style');
   style.innerHTML = `
        body {
            opacity: 0 !important;
            visibility: hidden !important;
            background-color: ${bgColor} !important;
        }
        body.is-loaded {
            opacity: 1 !important;
            visibility: visible !important;
            transition: opacity 0.6s ease-in-out !important;
        }
        #loader-progress-bar {
            position: fixed;
            top: 0;
            left: 0;
            height: 3px;
            background-color: ${barColor};
            z-index: 99999;
            width: 0%;
            transition: width 0.4s ease;
        }
    `;
   document.head.appendChild(style);

   // 3. –°—Ç–≤–æ—Ä—é—î–º–æ –µ–ª–µ–º–µ–Ω—Ç –ø—Ä–æ–≥—Ä–µ—Å-–±–∞—Ä—É
   const progressBar = document.createElement('div');
   progressBar.id = 'loader-progress-bar';
   document.documentElement.appendChild(progressBar);

   // –Ü–º—ñ—Ç–∞—Ü—ñ—è —Ä—É—Ö—É –ø—Ä–æ–≥—Ä–µ—Å—É
   let progress = 0;
   const interval = setInterval(() => {
      progress += Math.random() * 30;
      if (progress > 90) progress = 90; // —Ç—Ä–∏–º–∞—î–º–æ –Ω–∞ 90%, –ø–æ–∫–∏ –Ω–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç—å—Å—è –ø–æ–≤–Ω—ñ—Å—Ç—é
      progressBar.style.width = progress + '%';
   }, 150);

   window.addEventListener('load', function () {
      // –î–æ–≤–∞–Ω—Ç–∞–∂—É—î–º–æ –¥–æ 100%
      clearInterval(interval);
      progressBar.style.width = '100%';

      setTimeout(function () {
         document.body.classList.add('is-loaded');

         // –í–∏–¥–∞–ª—è—î–º–æ —Å–º—É–∂–∫—É –ø—ñ—Å–ª—è –ø–æ—è–≤–∏ –∫–æ–Ω—Ç–µ–Ω—Ç—É
         setTimeout(() => {
            progressBar.style.opacity = '0';
            setTimeout(() => progressBar.remove(), 400);
         }, 300);
      }, delay);
   });
}

// // –í–∏–∫–ª–∏–∫: (–∑–∞—Ç—Ä–∏–º–∫–∞, –∫–æ–ª—ñ—Ä —Ñ–æ–Ω—É, –∫–æ–ª—ñ—Ä —Å–º—É–∂–∫–∏)
