document.addEventListener("DOMContentLoaded", () => {
   const accordions = document.querySelectorAll("[data-accordion]");

   accordions.forEach((accordion) => {
      const btn = accordion.querySelector("[data-accordion-btn]");
      const body = accordion.querySelector("[data-accordion-body]");

      btn.addEventListener("click", (e) => {
         e.preventDefault();

         const isOpen = accordion.classList.contains("is-open");

         if (!isOpen) {
            // –í–Ü–î–ö–†–ò–¢–¢–Ø
            accordion.classList.add("is-open");
            // –í—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ –≤–∏—Å–æ—Ç—É –≤ px –¥–ª—è –∑–∞–ø—É—Å–∫—É transition
            body.style.height = `${body.scrollHeight}px`;

            // –ü—ñ—Å–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è –∞–Ω—ñ–º–∞—Ü—ñ—ó —Å—Ç–∞–≤–∏–º–æ auto –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—ñ
            body.addEventListener("transitionend", function handler() {
               if (accordion.classList.contains("is-open")) {
                  body.style.height = "auto";
               }
               body.removeEventListener("transitionend", handler);
            }, { once: true });

         } else {
            // –ó–ê–ö–†–ò–¢–¢–Ø
            // –°–ø–æ—á–∞—Ç–∫—É –º—ñ–Ω—è—î–º–æ auto –Ω–∞ —Ñ—ñ–∫—Å–æ–≤–∞–Ω—ñ px
            body.style.height = `${body.scrollHeight}px`;

            // –í–∏–∫–ª–∏–∫–∞—î–º–æ –ø–µ—Ä–µ—Ä–∞—Ö—É–Ω–æ–∫ (force reflow)
            body.offsetHeight;

            // –ó–∞–ø—É—Å–∫–∞—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –¥–æ 0
            requestAnimationFrame(() => {
               body.style.height = "0";
               accordion.classList.remove("is-open");
            });
         }
      });
   });
});
